  margin-left: 73%;
  width: 25%;
  height: 60px;
  /*border: 8px solid rgba(8,232,222);*/
}

h1 {
  float: right;
  margin-left: 10px;
  margin-top: 20px;
}

.profile-area img {
  float: right;
  display: inline-block;
  right: 10px;
  max-width: 50px;
  margin-bottom: 10px;
  color: white;
}

.profile-prof {
  float: left;
  display: inline-block;
  right: 10px;
  max-width: 50%;
  margin-left: 5px;
  margin-bottom: 10px;
  color: white;
  border: 4px solid black;
}

h3 {
  margin-top: 4%;
  margin-bottom: 4%;
}
.panel-body {
  margin-left: 5%;
  width: 100%;
  height: 100%;
}



