@import "https://fonts.googleapis.com/css?family=Lato:100, 300, 400, 700, 900";
@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.6.3/css/font-awesome.min.css";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
@import "https://fonts.googleapis.com/css?family=Lato:100, 300, 400, 700, 900";

body {
  background: #286da8;
  position: absolute;
  color: #ffffff;
  font-family: "Lato", sans-serif;
  height: 100%;
  width: 100%;
  text-align: left;
  /*border: 6px solid rgba(102,255,0);*/
}

.App-header {
  width: 100%;
  height: 100px;
  margin-bottom: 20px;
  background: #0b3c5d;
  border-bottom: 2px solid #f5f5f1;
  text-align: left;
  /*border: 2px solid rgba(255,0,127);*/
}

.App-navbar {
  margin-top: 1.5%;
  margin-left: 35%;
  width: 45%;
  height: 60px;
  background: #0b3c5d;
  border: none;
  /*border: 2px solid rgba(255,170,29);*/
}

.Button {
  float: left;
  background: transparent;
  height: 44px;
  text-decoration: none;
  color: #f5f5f1;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 44px;
  -webkit-transition: border 0.125s ease, background 0.125s ease;
  -o-transition: border 0.125s ease, background 0.125s ease;
  transition: border 0.125s ease, background 0.125s ease;
  font-size: 14px;
  font-weight: 600;
  display: inline-block;
  margin-right: 10px;
  text-align: center;
  vertical-align: middle;
  /*border: 2px solid rgba(0,153,204);*/
}
.Button:hover {
  border: 2px solid #f5f5f1;
}

.Button[data-primary="true"] {
  border: 2px solid #f5f5f1;
}
.Button[data-primary="true"]:hover {
  background: #0099cc;
  color: #f5f5f1;
}

@media (max-width: 767px) {
  .App-header {
    text-align: center;
    padding-bottom: 20px;
  }
}

@media (max-width: 991px) {
  .App-header {
    text-align: center;
  }
}



