.App-title {
  position: relative;
  text-align: center;
  width: 100%;
  font-weight: bold;
  line-height: 1.3;
  font-size: 40px;
  padding-bottom: 40px;
}

.image-info {
  margin: 20px 20px 10px 60px;
  width: 600px;
  height: 350px;
  padding: 1px;
  border: 1px solid #021a40;
  background-color: #67aeca;
}

#section-1 {
  float: right;
  margin: 45px 30px 10px 30px;
  width: 45%;
  height: 230px;
  font-weight: bold;
  line-height: 1.3;
  font-size: 30px;
  text-align: left;
}

#section-2 {
  float: left;
  padding-top: 80px;
  margin: 30px 30px 10px 60px;
  width: 45%;
  height: 230px;
  font-weight: bold;
  line-height: 1.3;
  font-size: 30px;
  text-align: left;
}

#section-3 {
  float: right;
  padding-top: 60px;
  margin: 30px 30px 10px 30px;
  width: 45%;
  height: 230px;
  font-weight: bold;
  line-height: 1.3;
  font-size: 30px;
  text-align: left;
}

#section-4 {
  float: left;
  padding-top: 80px;
  margin: 30px 30px 10px 60px;
  width: 45%;
  height: 400px;
  font-weight: bold;
  line-height: 1.3;
  font-size: 30px;
  text-align: left;
}

@media (max-width: 991px) {

    float: none;
    /*width: 100%;*/
    text-align: center;
  
  #section-1,
  #section-2,
  #section-3,
  #section-4 {
    margin: 30px 30px 10px 30px;
    padding-top: 0px;
    float: left;
    font-size: 25px;
    height: 100%;
    width: auto;
  }
  .image-info {
    margin: 10px 30px 10px 30px;
    width: auto;
    height: 250px;
    padding: 1px;
    border: 1px solid #021a40;
    background-color: #67aeca;
  }
  .App-title {
    font-size: 35px;
  }
}

@media (max-width: 767px) {

  float: none;
  /*width: 100%;*/
  text-align: center;
  padding-bottom: 20px;

  #section-1,
  #section-2,
  #section-3,
  #section-4 {
    padding-top: 0px;
    float: left;
    font-size: 20px;
    height: 100%;
    width: auto;
    margin-left: 10px;
  }
  .image-info {
    margin: 10px 30px 10px 10px;
    width: auto;
    height: 250px;
    padding: 1px;
    border: 1px solid #021a40;
    background-color: #67aeca;
  }
  .App-title {
    font-size: 30px;
  }
}



